@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");
.App {
  font-family: Circular, -apple-system, system-ui, Roboto, "Helvetica Neue",
    sans-serif;
  text-align: center;
}
a {
  color: unset;
  cursor: pointer;
  text-decoration: none;
}
img {
  height: 100%;
}
ul {
  margin: 0;
}
.navbar {
  position: fixed;
  width: 100%;
  background-color: rgb(18, 146, 79);
  z-index: 99999;
}

.navbar div {
  display: inline-block;
  cursor: pointer;
  padding: 10px;
  color: white;
}
.link {
  padding: 25px 40px !important;
  font-size: 20px;
}
.link:hover {
  background-color: rgb(23, 173, 96);
}
.scroll-component {
  padding-top: 73px;
}
.contact-container {
  padding: 15px;
  background-color: rgb(136, 224, 179);
  position: relative;
  color: white;
}
.contact-text {
  margin: 20px;
  margin-top: 0;
  font-size: 25px;
}
.contact-btn {
  border: none;
  background-color: rgb(18, 146, 79);
  color: white;
  padding: 1vw 2vw;
  font-size: 2vw;
  font-weight: 800;
  cursor: pointer;
  border-radius: 5px;
  margin-bottom: 20px;
}
.contact-btn:focus {
  outline: none;
}
.show {
  display: block;
}
.main {
  min-height: 550px;
}
.headline-container {
  padding-top: 15vw;
  text-align: -webkit-center;
}
.headline {
  font-size: 45px;
  font-weight: 600;
  padding: 15px;
}
.sub-headline {
  font-size: 35px;
  width: 60vw;
}
.highlight {
  background-color: #e8e5e5;
}
.lg-logos-container {
  position: relative;
  bottom: 4vw;
}
.tech-logo-sm {
  width: 75px;
  margin: 3vw;
}
.tech-logo-lg {
  width: 125px;
  margin: 2vw;
}
.about-headline {
  padding-bottom: 0;
}
.about-content-container {
  height: 30vw;
  background-color: rgb(18, 146, 79);
  width: 30vw;
  display: inline-block;
  position: relative;
  bottom: 7.5vw;
  left: 5vw;
}
.about-content {
  color: white;
  font-size: 2vw;
  font-weight: 200;
  padding: 4vw;
}
.odin-project {
  font-weight: 400;
}
.odin-project:hover {
  text-decoration: underline;
}
.headshot-container {
  height: 35vw;
  display: inline-block;
}
.about-main {
  margin: 50px;
}
.testimonials-container {
  padding: 1vw 5vw 10vw 5vw;
  margin-top: 50px;
  background-color: rgb(136, 224, 179);
  font-weight: 200;
}
.testimonials-headline {
  margin: 10px 0 25px 0;
}
.testimonial {
  padding: 0 40px;
  display: inline-block;
  width: 23vw;
  vertical-align: top;
}
.testimonial-text {
  font-size: 20px;
}
.testimonial-title {
  font-size: 90%;
}
.testimonial-info-container {
  display: inline-block;
  position: relative;
  bottom: 15px;
  opacity: 0.75;
  margin-top: 25px;
}
.testimonial-headshot {
  height: 35px;
  border-radius: 50px;
  display: inline-block;
  margin: 10px;
  margin-left: 0;
  vertical-align: middle;
}
.testimonial-id-container {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
}
.footer-container {
  margin: 100px 0;
}
.footer-item {
  display: inline-block;
  width: 23vw;
  vertical-align: top;
  text-align: -webkit-center;
  padding: 0 40px;
}
.footer-title {
  font-size: 25px;
  margin-bottom: 30px;
}
.footer-value {
  width: fit-content;
}
.work-container {
  margin-left: 20px;
}
.work-headline {
  margin-top: 0;
  color: black;
  padding: 30px;
  margin-bottom: 20px;
}
.work-item-container {
  padding-bottom: 100px;
  text-align: left;
  padding-left: 10vw;
}
.image-container {
  height: fit-content;
  display: inline-block;
  text-align: -webkit-center;
}
.work-item-photo {
  width: 35vw;
}
.work-item-info-container {
  display: inline-block;
  vertical-align: top;
  text-align: left;
  margin: 10px 4vw;
  margin-right: 0;
  font-size: 2vw;
  max-width: 38vw;
}
.work-item-title {
  font-size: 2.5vw;
}
.work-item-description {
  margin-top: 5px;
  color: rgb(87, 85, 85);
}
.work-item-skills {
  margin: 20px 0;
  color: rgb(87, 85, 85);
}
.follow-link {
  display: block;
  position: relative;
  bottom: 10px;
}
.follow-link:hover {
  text-decoration: underline;
}
@media screen and (max-width: 1200px) {
  .testimonial {
    width: fit-content;
  }
  .testimonials-container {
    padding-bottom: 5vw;
  }
  .work-item-info-container {
    margin-top: 0;
  }
}
.work-item-about-container {
  display: inline-block;
  height: 30vh;
  width: 400px;
  position: absolute;
  right: 7vw;
}
.link-container {
  height: 20px;
  width: fit-content;
}
.github {
  width: 2.5vw;
  height: 2.5vw;
}
.link-text {
  display: inline-block;
  position: relative;
  bottom: 5px;
  margin-left: 5px;
  font-size: 1.5vw;
}
.link-container:hover .link-text {
  text-decoration: underline;
}
@media screen and (max-width: 925px) {
  #about {
    display: none;
  }
  #experience {
    float: left;
  }
  #contact {
    float: right;
  }
  .link {
    padding: 25px !important;
  }
  .about-container {
    display: none;
  }
  .contact-container {
    margin-left: 0;
  }
  .headshot-container {
    height: 50vw;
  }
  .city-container {
    margin-top: 10px;
  }
  .testimonial {
    padding: 10px;
  }
  .testimonials-container {
    margin-bottom: 0;
    margin-top: 30px;
  }
  .testimonials-headline {
    margin-bottom: 10px;
  }
  .testimonial div {
    font-size: 15px;
  }
  .work-item-container {
    padding-bottom: 45px;
    margin-left: 0;
  }
  .work-item-photo {
    width: 40vw;
  }
  .work-item-info-container {
    text-align: center;
  }
  .work-item-title {
    font-size: 4vw;
  }
  .work-item-description {
    font-size: 3vw;
  }
  .work-item-skills {
    display: none;
  }
  .contact-btn {
    margin-top: 15px;
  }
  .work-container {
    margin-left: 0;
  }
  .footer-container {
    margin: 20px;
    text-align: -webkit-center;
  }
  .footer-item {
    display: block;
    margin-bottom: 20px;
    width: 100%;
  }
  .footer-title {
    margin-bottom: 10px;
  }
  .link-text {
    bottom: 3px;
  }
  .follow-link {
    position: unset;
  }
}
